/* OpenMoji END */

/* base */
:root {
    min-height: 100%;
    overflow-x: hidden;
    background: var(--bg);
    font-family: sans-serif;
    color: var(--text-color);
    accent-color: var(--main);
    /* other useful stuff: */
    --header-height: 4rem;

    /* TOOLTIP STUFF */
    /* offset moves tooltip to the center of the parent */
    --tp-offset: -0.15rem;
    /* make stuff easier for people that steal this CSS file */
    --tp-text-color: var(--text-color);
    --tp-border-color: var(--main-2);
    --tp-bg-color: var(--bg);
}

#skip-to-content {
    position: absolute;
    left: 1rem;
    top: 0;
    transform: translateY(-3rem);

    font-weight: bold;

    color: var(--text-color);
    background-color: var(--main);

    border-radius: 0 0 0.5rem 0.5rem;

    padding: 0 1rem 0 1rem;
    height: 2rem;

    z-index: 10;
}

#skip-to-content:focus {
    top: var(--header-height);
    transform: translateY(0);
}

body {
    margin: 0;
}

main {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: calc(100% - 4vw);
    width: 96vw;
    /* top, right, bottom, left */
    padding: 0.5rem 2vw 0 2vw;
}
/* .text is for version page */
main, .text, a {
    white-space: normal;
    line-break: auto;
    hyphens: auto;
}

.center-text {
    text-align: center;
}

a:not(.no-style-a, .no-style-a-block, .no-style-a-emoji) {
    color: var(--main)
}

.no-style-a, .no-style-a-block, .no-style-a-emoji {
    text-decoration: none;
    color: inherit;
}

.no-style-a:hover, .no-style-a-block:hover {
    text-decoration: underline;
}

.no-style-a-block {
    display: block;
}

.hidden {
    position: absolute;
    display: none;
    pointer-events: none;
    visibility: collapse;
    max-width: 0 !important;
    width: 0 !important;
    height: 0 !important;
    opacity: 0;
}
/* base end */


/* footer */
footer {
    width: 100vw;
    max-width: 100%;
    text-align: center;
    margin-top: 2.6rem;
    margin-bottom: 0.5rem;
}
/* footer end */


/* header */
header {
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;

    z-index: 2;

    overflow: hidden;

    width: 100vw;
    max-width: 100%;
    color: #fff;
    height: var(--header-height);
    background-color: var(--main);
}

#title, #back-to-home {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin: 0;
}

#title {
    font-size: 2rem;
    white-space: nowrap;
    left: calc(1rem + calc(var(--header-height) / 1.55));
}

#back-to-home {
    left: 0.5rem;
    border-color: var(--text-color);
    border-width: 0;
    border-style: solid;
    border-radius: 50%;
    background-color: var(--text-color);
    height: calc(var(--header-height) / 1.55);
    width: calc(var(--header-height) / 1.55);
}

