#back-to-home::before, #back-to-home::after {
    position: absolute;
    content: " ";
    width: 100%;
    height: 100%;
    clip-path: url(#kangaroo-face);
}

#back-to-home::before {
    background-color: var(--text-color);
    border-radius: 0 0 50% 50%;  /* only show ears */
}

#back-to-home:after {
    border-radius: 50%;
    background-color: var(--main);
}

@media (max-width:31rem) {
    #title {
        visibility: hidden;
    }
    #title::before {
        content:attr(short_title);
        visibility: visible;
    }
}

#open-pane {
    position: absolute;
    top: calc(calc(var(--header-height) - calc(var(--header-height) / 2)) / 2);
    right: calc(calc(var(--header-height) - calc(var(--header-height) / 2)) / 2);
    width: calc(var(--header-height) / 2);
    height: calc(var(--header-height) / 2);
}

#open-pane:hover, #open-pane:focus {
    transform: rotateY(30deg);
}

.site-pane-a {
    color: var(--main-2) !important;
}

#site-pane {
    right: -70%;
    top: 0;
    z-index: 11;
    position: fixed;
    background-color: var(--secondary-bg);
    max-width: 70%;
    height: 100vh;
    overflow-y: auto;

    transition: right 0.5s;
}

#site-pane[open],
    #site-pane:hover,
    #site-pane:focus-within,
    #open-pane:focus + #site-pane,
    #open-pane:hover + #site-pane {
    right: 0;
}

#site-pane h2 {
    margin: 0.625rem;
}

#site-pane ul {
    padding-right: 0.25em;
}

.list {
    list-style-type: none;
    padding-left: 1em;
}
.sub-list {
    list-style-type: none;
    padding-left: 2rem;
}
.list li, .sub-list li {
    display: block;
}
.list li::before, .sub-list li::before {
    content: "\2022";
    display: inline-block;
    height: available;
    margin-bottom: 0.1em;
    margin-right: 0.2rem;
}
.sub-list li::before {
    content: "\25AA";
    font-size: 0.6rem;
    position: relative;
}

.contact-anchor {
    display: block;
    margin: 1.25rem;
    padding: 0.625rem;
    text-align: center;
    background-color: var(--contrast);
    color: var(--text-color)!important;
}

.contact-anchor:hover {
    transform: scale(1.05, 1.05);
}

#oekostrom {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0.2rem 0;
}

#oekostrom img {
    height: auto;
    width: 8rem;
    max-width: 85%;
}
/* header end */

/* TOOLTIP START */
[tooltip] {
    position: relative;
}

[tooltip]::before, [tooltip]::after {
    /* position */
    position: absolute;
    /* on top of everything */
    z-index: 1000000000;
    /* make invisible */
    display: none;
    opacity: 0;
    visibility: hidden;
    /* ignore hover over tooltip so it disappears when leaving parent */
    pointer-events: none;
    /* reset font size, so it doesn't depend on parent */
    font-size: 1rem;
}

[tooltip]:hover::after,
[tooltip]:hover::before,
[tooltip]:focus::after,
[tooltip]:focus::before,
[tooltip].show-tooltip::after,
[tooltip].show-tooltip::before {
    /* make tooltip and triangle visible */
    display: inherit;
    opacity: 1;
    transition: all 0.05s ease 0.1s;
    visibility: visible;
}

/* top tooltip after and before */
[tooltip][tooltip-position=top]::after,
[tooltip][tooltip-position=top]::before,
/* no tooltip-position defaults to top */
[tooltip]:not([tooltip-position])::after,
[tooltip]:not([tooltip-position])::before,
/* bottom tooltip after and before */
[tooltip][tooltip-position=bottom]::after,
[tooltip][tooltip-position=bottom]::before {
    /* if position top or bottom center tooltip horizontally */
    left: 50%;
    transform: translateX(-50%);
}

/* left tooltip after and before */
[tooltip][tooltip-position=left]::after,
[tooltip][tooltip-position=left]::before,
/* right tooltip after and before  */
[tooltip][tooltip-position=right]::after,
[tooltip][tooltip-position=right]::before {
    /* if position left or right center tooltip vertically */
    top: 50%;
    transform: translateY(-50%);
}

/* the tooltip */
[tooltip]::after {
    /* the tooltip */
    content: attr(tooltip);

    hyphens: initial;
    letter-spacing: normal;
    text-align: center;
    white-space: pre-line;
    padding: 0.4rem;
    opacity: 1;

    background-color: var(--tp-bg-color);
    color: var(--tp-text-color);

    width: max-content;
    max-width: max(100%, 8.8rem);

    border-radius: 0.4rem;
    border-style: solid;
    border-color: var(--tp-border-color);
    border-width: thin;
}

[tooltip][tooltip-position=top]::after,
/* no tooltip-position defaults to top */
[tooltip]:not([tooltip-position])::after {
    /* if position top move the tooltip above the parent*/
    bottom: calc(100% - var(--tp-offset));
}

[tooltip][tooltip-position=bottom]::after {
    /* if position bottom move the tooltip below the parent */
    top: calc(100% - var(--tp-offset));
}

[tooltip][tooltip-position=left]::after {
    /* if position left move the tooltip left of the parent */
    right: calc(100% - var(--tp-offset));
}

[tooltip][tooltip-position=right]::after {
    /* if position right move the tooltip right of the parent */
    left: calc(100% - var(--tp-offset));
}

/* the triangle that points from tooltip to parent */
[tooltip]::before {
    content: " ";
    border-width: 0.3125rem;
    border-style: solid;
    /* set border-color defaults to override for each position */
    border-color: transparent transparent transparent transparent;
}

[tooltip][tooltip-position=top]::before,
/* no tooltip-position defaults to top */
[tooltip]:not([tooltip-position])::before {
    /* position top */
    top: var(--tp-offset);
    border-top-color: var(--tp-border-color);
}

[tooltip][tooltip-position=bottom]::before {
    /* position bottom */
    bottom: var(--tp-offset);
    border-bottom-color: var(--tp-border-color);
}

[tooltip][tooltip-position=left]::before {
    /* position left */
    left: var(--tp-offset);
    border-left-color: var(--tp-border-color);
}

[tooltip][tooltip-position=right]::before {
    /* position right */
    right: var(--tp-offset);
    border-right-color: var(--tp-border-color);
}
/* TOOLTIP END */
